@import url(https://fonts.googleapis.com/css?family=Ubuntu:300,300italic,regular,italic,500,500italic,700,700italic);
@import url(https://fonts.googleapis.com/css?family=Poppins:100,100italic,200,200italic,300,300italic,regular,italic,500,500italic,600,600italic,700,700italic,800,800italic,900,900italic);
@import url(https://fonts.googleapis.com/css?family=Merriweather:300,300italic,regular,italic,700,700italic,900,900italic);
@import url(https://fonts.googleapis.com/css?family=Baloo+2:regular,500,600,700,800);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    height: 100vh;
    height: -webkit-fill-available;
}

.App {
    height: 100vh;
    min-height: 100vh;
    min-height: -webkit-fill-available;
    font-family: "Poppins", "sans-serif";
    background-image: url(/static/media/Mountain.eb3b246f.png);
    background-position: left;
    background-size: cover;
}

.blur {
    -webkit-filter: blur(10px);
            filter: blur(10px);
}

.date {
    color: #fff;
    font-size: 1.2em;
    display: flex;
    margin-right: 8px;
    justify-content: flex-end;
}

.search-box {
    display: flex;
    margin-left: 8px;
    width: 85%;
    text-indent: 10px;
    border-radius: 20px;
    border: 1px solid rgba(0, 0, 0, 0.3);
    text-align: left;
    font-size: 23px;
    color: rgb(255, 255, 255);
    background-color: rgba(221, 217, 217, 0.678);
}

.search-box:focus {
    color: #fff;
    outline: none;
    box-shadow: 0px 0px 5px rgba(255, 255, 255, 0.3);
}

.search-box::-webkit-input-placeholder {
    color: rgba(58, 56, 56, 0.438);
}

.search-box:-ms-input-placeholder {
    color: rgba(58, 56, 56, 0.438);
}

.search-box::placeholder {
    color: rgba(58, 56, 56, 0.438);
}

.row-flex-header {
    padding-top: 15px;
    display: flex;
}

.superscript {
    font-size: 30px;
}

.forecast-preview {
    padding-top: 40px;
}

.forecast-col {
    display: flex;
    justify-content: center;
    align-items: center;
}
.citystate {
    color: #fff;
    font-size: 2.5em;
    /* font-weight: 650; */
    padding-bottom: 0px;
}

.time {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-size: 1.3em;
    /* padding-bottom: 15px; */
}

.temp {
    color: #fff;
    font-size: 7em;
    padding-bottom: 0px;
    font-weight: 650;
}

.weather-box {
    /* background-color: rgba(255, 255, 255, 0.65); */
    width: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 150px;
    border-radius: 30px 100px;
}

.flex {
    display: flex;
    align-items: center;
    justify-content: center;
}

.now {
    margin-left: 25px;
    margin-bottom: 10px;
    color: #fff;
    font-size: 1.2em;
    font-weight: 400;
}

.weather {
    width: 100%;
    padding-top: 0px;
    padding-bottom: 0px;
}

/* Forecast Cards */

.scroll-weather .weather-card {
    width: 150px;
    display: inline-block;
    color: rgb(250, 250, 250);
    text-align: center;
    margin: 0px;
    padding: 0px 15px;
    text-decoration: none;
    overflow: hidden;
    border-right: 1px solid rgba(255, 255, 255, 0.15);
}

.scroll-weather {
    overflow: auto;
    white-space: nowrap;
}

.future {
    /* background-color: rgba(255, 255, 255, 0.534); */
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    white-space: nowrap;
    /* margin-top: 80px; */
    height: 100px;
    width: 100%;
    /* padding-bottom: 50px; */
    border-top: 1px solid rgba(255, 255, 255, 0.35);
    /* border-radius: 20px 40px; */
    /* box-shadow: rgba(240, 235, 235, 0.5)2px 3px 10px; */
}

.test {
    position: absolute;
    bottom: 0;
}


